import { Modal } from 'bootstrap';
import axios from 'axios';
import { XMLParser, XMLBuilder } from 'fast-xml-parser';

const options = {
	ignoreAttributes: true,
};

const parser = new XMLParser(options);
const builder = new XMLBuilder(options);

function stringToFilm(string) {
	const filmElements = string.split('&#44;');
	const film = {
		id: filmElements[0],
		title: filmElements[1],
		year: filmElements[2],
		director: filmElements[3],
		stars: filmElements[4],
		review: filmElements[5],
	};
	return film;
}

function stringToFilms(string) {
	const filmsString = string.split(/\r?\n/);
	let films = [];
	filmsString.forEach((row) => {
		const film = stringToFilm(row);
		films.push(film);
	});
	return films;
}

function validateForm(form) {
	form.target.classList.add('was-validated');
	if (!form.target.checkValidity()) {
		form.preventDefault();
		form.stopPropagation();
		return false;
	}
	return true;
}

const headers = {
	json: {
		Accept: 'application/json',
		'content-type': 'application/json',
	},
	text: {
		Accept: 'text/plain',
		'content-type': 'text/plain',
	},
	xml: {
		Accept: 'application/xml',
		'content-type': 'application/xml',
	},
};

const app = Vue.createApp({
	data() {
		return {
			films: [],
			film: {
				id: 0,
				title: '',
				year: 2022,
				director: '',
				stars: '',
				review: '',
			},
			dataType: 'xml',
			modal: null,
			searchBy: 'filmTitle',
			searchValue: '',
			pagination: {
				currentPage: 0,
				filmsPerPage: 10,
				totalFilms: 0,
				pageRange: 2,
			},
		};
	},
	created() {
		//this.getFilms();
		//this.getFilm();
	},
	methods: {
		async getFilm(filmID) {
			const config = {
				method: 'GET',
				headers: headers[this.dataType],
				params: {
					filmID: filmID,
				},
			};
			await axios('/api/film', config)
				.then((response) => {
					switch (this.dataType) {
						case 'json':
							this.films.push(response.data);
							break;
						case 'xml':
							this.films.push(parser.parse(response.data).Film);
							break;
						case 'text':
							this.films.push(stringToFilm(response.data));
							break;
						default:
							throw new Error('Error');
					}
				})
				.catch((error) => {
					console.log(error);
				});
		},
		async getFilms(filmTitle = null) {
			let config = {
				method: 'GET',
				headers: headers[this.dataType],
			};
			if (filmTitle) {
				config.params = {
					filmTitle: filmTitle,
				};
			}
			await axios('/api/films', config)
				.then((response) => {
					switch (this.dataType) {
						case 'json':
							this.films = response.data.films;
							break;
						case 'xml':
							let xmlParse = parser.parse(response.data).Films;
							if (Array.isArray(xmlParse.Film)) xmlParse = xmlParse.Film;
							this.films = xmlParse;
							break;
						case 'text':
							this.films = stringToFilms(response.data);
							break;
						default:
							throw new Error('Error');
					}
				})
				.catch((error) => {
					console.log(error);
				});
		},
		async searchFilms(form) {
			if (validateForm(form)) {
				this.searchBy == 'filmTitle' ? await this.getFilms(this.searchValue) : await this.getFilm(this.searchValue);
				this.modal.hide();
			}
		},
		async addFilm(method) {
			let filmData = builder.build({ Film: this.film });
			const config = {
				method: method,
				headers: headers[this.dataType],
				data: filmData,
			};
			await axios('/api/update', config).then((response) => {
				console.log(response.data);
			});
		},
		showModal(modalID) {
			this.modal = new Modal(document.getElementById(modalID));
			this.modal.show();
		},
		submitFilm(form) {
			if (validateForm(form)) {
				this.addFilm('POST');
			}
		},
	},
});

app.mount('#app');
